body{
    margin: 0;
}
@media (max-aspect-ratio:7/10) {
    .joinPage{

        display: flex;
        justify-content: center;
        align-items: flex-start !important;
    }
    .JoinContainer{
        margin-top: 40vw !important;
        width: 100% !important; 
    }
    .JoinContainer >img{
        width: 30vw !important;  
    }
    .JoinContainer > h1{
    font-size: 4vw !important;

    }
    #joinInput{
        width: 60vw !important;
        padding: 2.8vw !important;
        font-size: 2.9vw !important;
        
    }
    .joinbtn{
        width: 60vw !important;
        padding: 2.3vw !important;
        font-size: 4.8vw !important;
        
        
    }
    .chatContainer{
        height: 100% !important;
        width: 100% !important;
    }
    .JoinContainer > h1{
        font-size: 5vw !important;
    }
    .header{
        height: 10% !important;
        
    }
    .header>h2{
        margin: 5vw !important;
    }
    .header>a>img{
        margin: 7vw !important;
    }
    .chatBox{
        height: 80% !important;    
    }
    .inputBox{
        height: 10% !important;
    }
    #chatInput{
        margin: 5.5vw !important;
        padding: 5.5vw !important;
        font-size: 4.5vw !important;
    
    }
    .sendBtn>img{
        margin-right: 2.3vw !important;
        
    }
    .messageBox{
        /* width: initial!important; */
        padding: 3vw !important;
        margin: 3vw !important;
        border-radius: 0.5vw;
        font-size: 4.2vw !important;
    }
    .header>p{
        font-size: 5vw !important;
    }
}